// @ts-nocheck
export interface SearchProps {
	/**
		* 自定义右侧操作按钮文字
		*/
	action ?: string;
	/**
		* 键盘弹起时，是否自动上推页面
		*/
	adjustPosition : boolean;
	/**
		 * 强制 input 处于同层状态，默认 focus 时 input 会切到非同层状态 (仅在 iOS 下生效)
		 */
	alwaysEmbed : boolean;
	/**
		 * 是否居中
		 */
	center : boolean;
	// inputAlign : 'left' | 'center' | 'right';
	/**
		* 是否启用清除控件
		*/
	clearable : boolean;
	/**
		 * 点击键盘右下角按钮时是否保持键盘不收起
		 */
	confirmHold : boolean;
	/**
		* 设置键盘右下角按钮的文字，仅在type='text'时生效。<br />具体释义：<br />`send` 右下角按钮为“发送”；<br />`search` 右下角按钮为“搜索”；<br />`next` 右下角按钮为“下一个”；<br />`go` 右下角按钮为“前往”；<br />`done` 右下角按钮为“完成”。<br />[小程序官方文档](https://developers.weixin.qq.com/miniprogram/dev/component/input.html)
		*/
	confirmType : 'send' | 'search' | 'next' | 'go' | 'done';
	/**
		 * 指定 focus 时的光标位置
		 */
	cursor ?: number;
	/**
		 * 搜索框聚焦时底部与键盘的距离
		 */
	cursorSpacing : number;
	/**
		* 是否禁用
		*/
	disabled : boolean;
	/**
		* 是否聚焦
		*/
	focus : boolean;
	/**
		 * focus时，点击页面的时候不收起键盘
		 */
	holdKeyboard : boolean;
	/**
		* 左侧图标。
		*/
	leftIcon : string;
	/**
		 * 用户最多可以输入的字符个数，一个中文汉字表示两个字符长度。`maxcharacter` 和 `maxlength` 二选一使用
		 */
	maxcharacter ?: number;
	/**
		* 用户最多可以输入的文本长度，一个中文等于一个计数长度。默认为 -1，不限制输入长度。`maxcharacter` 和 `maxlength` 二选一使用
		*/
	maxlength : number;
	/**
		* 占位符
		*/
	placeholder : string;
	/**
		* 指定 placeholder 的样式类
		*/
	placeholderClass ?: string;
	/**
		 * 指定 placeholder 的样式
		 */
	placeholderStyle ?: string;
	/**
		* 光标结束位置，自动聚集时有效，需与 selection-start 搭配使用
		*/
	selectionEnd : number;
	/**
		 * 光标起始位置，自动聚集时有效，需与 selection-end 搭配使用
		 */
	selectionStart : number;
	/**
		 * 搜索框形状
		 */
	shape : 'square' | 'round';
	/**
		 * 拉起键盘的类型
		 */
	type : 'text' | 'number' | 'idcard' | 'digit' | 'nickname';
	/**
		* 值
		*/
	value ?: string;
	lStyle?: string;
	cursorColor?: string;
	padding?: string;
	radius?: string;
	height?: string;
	bgColor?: string;
	fontSize?: string;
	textColor?: string;
	iconColor?: string;
	clearIconColor?: string
}